<template>
	<view class="page">
		<view class="input_1"><u--input placeholder="请输入学号" v-model="username" border="none" fontSize="18"></u--input></view>
		<view class="input_1">
			<u--input placeholder="请输入密码" v-model="password" border="none" fontSize="18" :password="pwd">
				<template slot="suffix">
					<u-icon :name="pwd ? 'eye-off' : 'eye'" size="33" @click="pwd = !pwd"></u-icon>
				</template>
			</u--input>
		</view>

		<!-- 	<view class="input_1">
			<u--input placeholder="请输入验证码" v-model="code" border="none" fontSize="18">
				<template slot="suffix">
					<image :src="codeUrl" @click="getCode" mode="widthFix" class="login-code-img"></image>
				</template>
			</u--input>
		</view> -->
		<!-- 		<u-radio-group v-model="value3" placement="row" size="24" labelSize="24">
			<u-radio activeColor="#5d4fdc" label="男"></u-radio>
			<u-radio activeColor="#5d4fdc" label="女"></u-radio>
		</u-radio-group> -->
		<view class="button_1" @click="goIndex">登录</view>
	</view>
</template>

<script>
import { login, getCodeImg } from '@/api/first-info.js';
export default {
	data() {
		return {
			username: '',
			password: '',
			pwd: true,
			codeUrl: null,
			uuid: null,
			captchaEnabled: null
		};
	},
	onLoad() {
		this.getCode();
	},
	methods: {
		goIndex() {
			if (!this.username || !this.password) {
				uni.showToast({
					duration: 2000,
					title: '学号或密码不能为空',
					icon: 'none'
				});
				return;
			}
			login({ username, password }).then(res => {
				console.log(res);
				uni.switchTab({
					url: '/pages/index/index'
				});
			});
		},
		getCode() {
			getCodeImg().then(res => {
				this.captchaEnabled = res.captchaEnabled === undefined ? true : res.captchaEnabled;
				if (this.captchaEnabled) {
					this.codeUrl = 'data:image/gif;base64,' + res.img;
					this.uuid = res.uuid;
				}
			});
		}
	}
};
</script>

<style lang="scss" scoped>
.input_1 {
	width: 630rpx;
	margin: 40rpx 0 0 32rpx;
	padding: 0 30rpx;
	background-color: rgba(248, 248, 248, 1);
	border-radius: 16rpx;
	height: 120rpx;
}
.button_1 {
	border-radius: 48rpx;
	width: 448rpx;
	height: 96rpx;
	margin: 40rpx auto;
	font-size: 32rpx;
	font-weight: 600;
	text-align: center;
	line-height: 96rpx;
	color: rgba(255, 255, 255, 1);
	// background-color: #dfe1e5;
	background-color: rgba(93, 79, 220, 1);
}
/deep/.u-radio-group {
	flex: 0 !important;
}
/deep/.u-radio-group--row {
	margin: 40rpx 130rpx 0;
	justify-content: space-around;
	height: 120rpx;
}
/deep/.u-input {
	height: 100%;
}
.login-code-img {
	width: 200rpx;
	height: 60rpx;
}
</style>
